.modal {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;

  background: #1c1c1f;
  visibility: hidden;
  opacity: 0;

  transition: all 0.3s ease;
}
.modal.open {
  visibility: visible;
  opacity: 1;

  transition-delay: 0s;
}
