  position: absolute;
  top: 15px;
  right: 15px;

  font-weight: bold;
  color: #f2cc91;

  background: none;
  border: 0;
  outline: none;
  cursor: pointer;

  appearance: none;
}

.modal__next,
.modal__prev {
  position: absolute;
  top: 50%;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 30px;
  height: 30px;

  background-color: #d2aa74;
  border: none;
  transform: translateY(-50%);
